import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Footer from '../../views/Footer';
import MainAppBar from '../../views/MainAppBar';
import LandingLayout from '../../layouts/LandingLayout';
import CardProductVertical from '../account/cardProductVertical';
import { productActions } from '../../store/actions/product.action';
import { connect } from 'react-redux';
import LoadingCircle from '../../components/Progress/loadingCircle';

import image1 from '../../../assets/banners.jpg';

const styles = theme => ({
	appBar: {
		position: 'relative'
	},
	icon: {
		marginRight: theme.spacing(2)
	},
	heroUnit: {
		backgroundColor: '#e6e6e6'
	},
	heroContent: {
		// maxWidth: 600,
		margin: '0 auto',
		// padding: theme.spacing(8, 0, 6),
		maxHeight: 380,
		display: 'flex',
		justifyContent: 'center'
	},
	heroButtons: {
		marginTop: theme.spacing(4)
	},
	layout: {
		width: 'auto',
		marginLeft: theme.spacing(3),
		marginRight: theme.spacing(3),
		[theme.breakpoints.up(1100 + theme.spacing(3) * 2)]: {
			width: 1100,
			marginLeft: 'auto',
			marginRight: 'auto'
		}
	},
	cardGrid: {
		padding: theme.spacing(8, 0)
	},

	footer: {
		backgroundColor: theme.palette.background.paper,
		padding: theme.spacing(6)
	}
});

function LandingPage(props) {
	const { classes, dataList, loadingList, location } = props;

	React.useEffect(() => {
		let urlFilter = location.pathname.split('/');
		if (urlFilter[2]) {
			props.handleListProduct(undefined, 'loadbrand', urlFilter[2]);
		} else {
			props.handleListProduct(undefined, 'loadbrand');
		}
	}, [props.location]);

	return (
		<LandingLayout>
			<main style={{ marginTop: '64px' }}>
				{location.state ? (
					''
				) : (
					<div className={classes.heroUnit}>
						<div className={classes.heroContent}>
							<img src={image1} alt='logos' style={{ width: '90%' }} />
						</div>
					</div>
				)}
				<div className={clsx(classes.layout, classes.cardGrid)}>
					<Grid container spacing={5}>
						{loadingList ? (
							<LoadingCircle generalCenter />
						) : (
							dataList && (
								<CardProductVertical
									listElement={dataList}
									handleAddCardProduct={product => props.handleProductAdd(product)}
								/>
							)
						)}
					</Grid>
				</div>
			</main>
		</LandingLayout>
	);
}

const mapStateToProps = ({ productReducer }) => {
	return {
		loadingList: productReducer.loadingList,
		dataList: productReducer.dataList,
		errorList: productReducer.errorList
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleListProduct: (myproduct, load, filter) => {
			dispatch(productActions.ListProduct(myproduct, load, filter));
		},
		handleProductAdd: product => {
			dispatch(productActions.AddProductCar(product));
		}
	};
};

LandingPage.propTypes = {
	classes: PropTypes.object.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LandingPage));
