import React from 'react';
import {
	Hidden,
	CardMedia,
	withStyles,
	Grid,
	CardContent,
	Typography,
	Card
} from '@material-ui/core';
import { Link, withRouter } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import CardActions from '@material-ui/core/CardActions';
import AspectRatioIcon from '@material-ui/icons/AspectRatio';
import BatteryStdIcon from '@material-ui/icons/BatteryStd';
import CameraIcon from '@material-ui/icons/Camera';
import StorageIcon from '@material-ui/icons/Storage';
import MemoryIcon from '@material-ui/icons/Memory';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import { productActions } from '../../store/actions/product.action';
import { connect } from 'react-redux';

const styles = theme => ({
	card: {
		height: '100%',
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center'
	},
	// cardMedia: {
	// 	paddingTop: '56.25%' // 16:9
	// },
	cardMedia: {
		width: '230px',
		height: '230px',
		backgroundRepeat: 'no-repeat',
		/* background: url(../img/imagen.jpg), */
		backgroundSize: 'cover',
		backgroundPosition: 'center'
	},
	links: {
		textDecoration: 'none',
		color: 'inherit'
	},
	cardAction: {
		width: '100%',
		justifyContent: 'flex-end'
	},
	itemProperty: {
		flexDirection: 'row',
		justifyContent: 'start',
		alignItems: 'center',
		'& > p': {
			margin: '0px',
			marginLeft: '8px'
		}
	},
	cardContent: {
		flexGrow: 1
	}
});

function CardProduct(props) {
	const { classes, listElement, horizontal } = props;

	const detailProd = product => {
		props.history.push({ pathname: '/detail', state: { product } });
	};
	const addCardProd = product => {
		props.handleAddCardProduct(product);
	};

	return (
		<Grid container spacing={2} className={classes.cardGrid}>
			{listElement.productResult.map(produc => (
				<Grid item key={produc._id} sm={6} md={4} lg={3}>
					<Card className={classes.card}>
						<MyCardMedia image={produc.image} classesStyle={classes.cardMedia} />
						<CardContent className={classes.cardContent}>
							<Grid container direction='row' justify='space-between' alignItems='center'>
								<Typography component='h2' variant='h5'>
									{produc.brand.name}
								</Typography>
								<Typography variant='subtitle1' color='textSecondary'>
									${produc.price}
								</Typography>
							</Grid>
							<Typography variant='subtitle1' color='textSecondary'>
								{produc.model.name}
							</Typography>
							<Grid container style={{ padding: '0px 5px' }}>
								<Grid item container className={classes.itemProperty} sm={6}>
									<AspectRatioIcon />
									<Typography variant='subtitle1' paragraph>
										{produc.screen.$numberDecimal} "
									</Typography>
								</Grid>
								<Grid item container className={classes.itemProperty} sm={6}>
									<CameraIcon />
									<Typography variant='subtitle1' paragraph>
										{produc.camera} mpx
									</Typography>
								</Grid>
								<Grid item container className={classes.itemProperty} sm={6}>
									<StorageIcon />
									<Typography variant='subtitle1' paragraph>
										{produc.storage} GB
									</Typography>
								</Grid>
								<Grid item container className={classes.itemProperty} sm={6}>
									<MemoryIcon />
									<Typography variant='subtitle1' paragraph>
										{produc.memory} GB
									</Typography>
								</Grid>
								<Grid item container className={classes.itemProperty} sm={6}>
									<BatteryStdIcon />
									<Typography variant='subtitle1' paragraph>
										{produc.battery} mAh
									</Typography>
								</Grid>
							</Grid>
						</CardContent>
						<CardActions className={classes.cardAction}>
							<Button size='small' color='primary' onClick={() => addCardProd(produc)}>
								ADD
							</Button>
						</CardActions>
					</Card>
				</Grid>
			))}
		</Grid>
	);
}

export default withRouter(withStyles(styles)(CardProduct));

function MyCardMedia({ image, classesStyle }) {
	const mergeUrl = () => {
		if (image) {
			return `${process.env.REACT_APP_API_HOST}` + image;
		} else {
			return 'data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22288%22%20height%3D%22225%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20288%20225%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_164edaf95ee%20text%20%7B%20fill%3A%23eceeef%3Bfont-weight%3Abold%3Bfont-family%3AArial%2C%20Helvetica%2C%20Open%20Sans%2C%20sans-serif%2C%20monospace%3Bfont-size%3A14pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_164edaf95ee%22%3E%3Crect%20width%3D%22288%22%20height%3D%22225%22%20fill%3D%22%2355595c%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%2296.32500076293945%22%20y%3D%22118.8%22%3EThumbnail%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E'; // eslint-disable-line max-len
		}
	};

	return <CardMedia className={classesStyle} image={mergeUrl(image)} title='Image title' />;
}
